import React from 'react'
import emailjs from 'emailjs-com'

function Farm() {

  const sendEmail=(e)=>{
    e.preventDefault()
    emailjs.sendForm('service_e7e2kf2','template_wgrdnbj',e.target,'81nJrVvG8YQzZ2W5c')
    
    
  }

  return (
    <section className="relative block py-24 lg:pt-0 bg-black">
    <div className='container border bg-green-500'>
        <h1  style={{marginLeft:'200px'}}>Book a Consultation</h1>
        <form className='row p-6 bg-green-500' onSubmit={sendEmail}>
            <label>Name</label>
            <input type='text' name='name' className='form-control w-4'/>
            <label>Email</label>
            <input type='email' name='user_email' className='form-control'/>
            <label>Message</label>
            <textarea type='text' rows='4' className='form-control' name='message' />
            <input type='submit' value='Send' className='btn btn-primary bg-black form-control mt-4' />
        </form>
    </div>
    </section>
  )
}

export default Farm